
import XHeader from 'vux/src/components/x-header/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/zip';
import 'rxjs/add/observable/forkJoin';
import wx from 'weixin-js-sdk';
import weixin from '@/services/weixin';

export default {
components: {
XHeader,
Flexbox,
FlexboxItem
},
directives: {
clipping: {
inserted: (el, binding) => {
const img = new Image();
img.onload = () => {
if (img.width > img.height) {
el.style.height = '100%';
} else {
el.style.width = '100%';
}
el.src = binding.value;
};
img.src = binding.value;
}
}
},
data() {
return {
list: [
{
title: ' - ',
date: '45',
content:
'3DDavid Sobral'
},
{
title: ' - ',
date: '45',
content:
'3DDavid Sobral'
},
{
title: '',
date: '45',
content:
'3DDavid Sobral'
},
{
title: ' - ',
date: '45',
content:
'3DDavid Sobral'
},
{
title: ' - ',
date: '45',
content:
'3DDavid Sobral'
},
{
title: '',
date: '45',
content:
'3DDavid Sobral'
},
{
title: ' - ',
date: '45',
content:
'3DDavid Sobral'
},
{
title: ' - ',
date: '45',
content:
'3DDavid Sobral'
},
{
title: '',
date: '45',
content:
'3DDavid Sobral'
}
],
imgs: [
require('@/assets/images/preview.jpg'),
require('@/assets/images/preview1.jpg'),
require('@/assets/images/preview2.jpg'),
require('@/assets/images/preview3.jpg')
],
imgIds: []
};
},
mounted() {
weixin.init(['chooseImage', 'uploadImage']);
},
methods: {
chooseImage() {
wx.chooseImage({
count: 9, // 9
sizeType: ['original', 'compressed'], // 
sourceType: ['album', 'camera'], // 
success: res => {
let oblistWX = [];
(res.localIds || []).map(localId => oblistWX.push(this.uploadImage(localId)));
Observable.zip(...oblistWX).subscribe(serverIds => {
let oblistDJ = [];
serverIds.map(serverId => oblistDJ.push(this.pictureUpload(serverId)));
Observable.forkJoin(...oblistDJ).subscribe(pictureIds =>
this.imgIds.push('pictureIds:' + pictureIds)
);
});
}
});
},
uploadImage: localId =>
Observable.create(observer =>
wx.uploadImage({
localId: localId, // IDchooseImage
isShowProgressTips: 1, // 1
success: res => {
observer.next(res.serverId);
observer.complete();
}
})
),
pictureUpload: serverId =>
Observable.create(observer =>
this.$http
.get('picture/upload', {
params: {
mediaId: serverId
}
})
.then(result => {
observer.next(result.data);
observer.complete();
})
)
}
};



